import { useState } from "react";

function Folder({ menuItems }) {
  const [expand, setExpand] = useState(false);
  if (menuItems.isFolder) {
    return (
      <>
        <div
          onClick={() => {
            setExpand(!expand);
          }}
        >
          {menuItems.name}
        </div>
        <div
          style={{
            display: expand ? "block" : "none",
            paddingLeft: "15px"
          }}
        >
          {menuItems.items.map((items) => {
            return <Folder menuItems={items} />;
          })}
        </div>
      </>
    );
  } else {
    return (
      <span>
        {menuItems.name}
        <br />
      </span>
    );
  }
}

export default Folder;
